body {
  text-align: center;
  background: #141E30;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  font-family: 'Fjalla One', sans-serif;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  padding-bottom: 20px;
  padding-top: 30px;
}

.title_img {
  max-width: 120px;
  max-height: 120px;
  padding-right: 10px;
}

.sub_title {
  font-size: 25px;
  padding-bottom: 100px;
}

.city_input {
  display: block;
  margin: 0 auto;
  padding: 20px;
  font-size: 20px;
  width: 500px;
  margin-bottom: 20px;
  border-radius: 10px 10px 10px 10px;
}

.city_input:focus {
  outline: none;
}

.result {
  font-size: 20px;
  transition: all 0.3s ease;
}

.city_name {
  padding-bottom: 10px;
  font-size: 36px;
  font-weight: 700;
}

.date {
  padding-bottom: 30px;
}

.general {
  padding-bottom: 20px;
}

.temp {
  padding-bottom: 20px;
}

.wind {
  padding-bottom: 20px;
}